<template>
  <div class="avatar">
    <img src="../assets/behindpic.png" id="backImg" />
    <canvas
      width="884"
      height="890"
      id="canvas"
    ></canvas>
  </div>
</template>

<script>
export default {
  name: "Avatar",
  props: {},
  methods: {},
  mounted: function () {
    const init = function (img) {
      const t = 1000;
      let [w, h] = [img.width, img.height];
      const cover = document.querySelector("#canvas");
      cover.height = h;
      cover.width = w;
      const viewportOffset = img.getBoundingClientRect();
      let [top, left] = [viewportOffset.top, viewportOffset.left];
      const ctx = cover.getContext("2d");
      const drawCover = function () {
        ctx.clearRect(0, 0, w, h);
        ctx.drawImage(img2, 0, 0, w, h);
      };
      let isDrawing = true;
      let path = [];
      const drawLine = function (now) {
        ctx.globalCompositeOperation = "source-over";
        drawCover();
        path = path.filter((p) => now - p.time < t);
        ctx.beginPath();
        ctx.lineCap = "round";
        ctx.globalCompositeOperation = "destination-out";
        ctx.lineWidth = 100;
        //         console.log('path length', path.length);
        for (let p of path) {
          const alpha = 1 - (now - p.time) / t;
          //console.log('alpha: ', alpha);
          ctx.strokeStyle = `rgba(0, 100, 0, ${alpha})`;
          ctx.lineTo(p.x, p.y);
        }
        ctx.stroke();
        requestAnimationFrame(drawLine);
      };

      const startFunc = function (event) {
        event.preventDefault();
        isDrawing = true;
        ctx.moveTo(
          (event.clientX || event.changedTouches[0].clientX) - left,
          (event.clientY || event.changedTouches[0].clientY) - top
        );
      };
      cover.addEventListener("mousedown", startFunc);
      cover.addEventListener("touchstart", startFunc);
      const moveFunc = function (event) {
        event.preventDefault();
        requestAnimationFrame(() => {
          if (isDrawing) {
            const point = {
              x: (event.clientX || event.changedTouches[0].clientX) - left,
              y: (event.clientY || event.changedTouches[0].clientY) - top,
              time: performance.now(),
            };
            console.log(`x:${point.x} y:${point.y}`);
            console.log(`w:${w} h:${h}`);
            path.push(point);
          }
        });
      };
      cover.addEventListener("mousemove", moveFunc);
      cover.addEventListener("touchmove", moveFunc);

      const upFunc = function (event) {
        event.preventDefault();
        // isDrawing = false;
      };
      cover.addEventListener("mouseup", upFunc);
      cover.addEventListener("touchend", upFunc);
      const img2 = new Image();
      img2.src = require("../assets/firstpic.png");
      img2.onload = () => {
        // document.body.append(cover);
        requestAnimationFrame(drawLine);
      };
      //调整窗口大小后调整坐标系
      window.addEventListener("resize", function () {
        setTimeout(() => {
          [w, h] = [img.width, img.height];
          cover.height = h;
          cover.width = w;
          const viewportOffset = img.getBoundingClientRect();
          [top, left] = [viewportOffset.top, viewportOffset.left];
          isDrawing = false;
          path = [];
        }, 0);
      });

      window.addEventListener("scroll", function () {
        setTimeout(() => {
          [w, h] = [img.width, img.height];
          cover.height = h;
          cover.width = w;
          const viewportOffset = img.getBoundingClientRect();
          [top, left] = [viewportOffset.top, viewportOffset.left];
          isDrawing = false;
          path = [];
        }, 0);
      });
    };
    const backImage = document.querySelector("#backImg");
    if (backImage.complete) {
      init(backImage);
    }
    backImage.onload = function () {
      init(backImage);
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@media only screen and (max-width: 600px) {
  img {
    width: 100%;
    height: auto;
  }
}

@media only screen and (min-width: 600px) {
  img {
    max-height: 768px;
    width: auto;
  }
}

.avatar {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

#backImg {
  position: absolute;
  right: 0;
  top: 0;
}

#canvas {
  position: absolute;
  right: 0;
  top: 0;
}
</style>
